.customSuccess-button {
  padding: 10px 20px;
  background-color:#28b463;
  border: none;
  color: white;
  font-size: 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.customSuccess-button:hover {
  background-color: #28b463;
}


